					</svg>
					Generar Excel
				</b-button>
			</vue-excel-xlsx>
			<!--<b-button
		@click="testFetch2"
		class="mb-4"
		style="margin-left: 0.5em"
		title="Mostrar"
		variant="success"
	>
		<v-icon dark style="color: black">mdi-check</v-icon>
		Mostrar Saldos
	</b-button>-->
