							<b>{{ data.value }}</b>
						</template>

						<template slot="cell(nombre)" slot-scope="data">
							{{ data.value.toUpperCase() }}
						</template>

						<template slot="cell(categoria)" slot-scope="data">
							{{ getCategoria(parseInt(data.value.split('/')[4])).letra}}
						</template>

						<template slot="cell(id_reparto)" slot-scope="data">
							{{ data.value.split('/')[4]}}
						</template>

						<template slot="cell(provincia)" slot-scope="data">
