													  style="font-size: 0.82em">
												DEPARTAMENTO
											</b-button>
										</b-card-header>
										<b-collapse id="accordion-filter-1"
													visible
													accordion="my-accordion"
													role="tabpanel">
											<b-card-body>
												<b-form-group id="input-group-4">
													<v-autocomplete id="provincia"
																	v-model="filter_provincia"
																	:items="options_deptos"
																	type="text"
																	solo
																	filled></v-autocomplete>
													<div v-show="filter_provincia != null">
														a
														<b-button @click="filter_provincia = null"
																  title="Limpiar">
															Limpiar
														</b-button>
													</div>
												</b-form-group>
											</b-card-body>
										</b-collapse>

										<b-card-header header-tag="header" class="p-1" role="tab">
											<b-button block
													  v-b-toggle.accordion-filter-2
													  variant="info"
													  style="font-size: 0.82em">
												LOCALIDAD
											</b-button>
										</b-card-header>
										<b-collapse id="accordion-filter-2"
													visible
													accordion="my-accordion"
													role="tabpanel">
											<b-card-body>
												<b-form-group id="input-group-4">
													<v-autocomplete id="localidad"
																	v-model="filter_localidad"
