			</b-modal>

			<!-- ==================================CREAR CARNET================================== -->
			<b-modal size="lg" ref="modal-carnetCliente" id="modal-carnetCliente" hide-footer>
				<template #modal-title>
					<h5 class="modal-title">Vista Previa</h5>
				</template>
				<clientes-carnet :carnet="carnetAPDF" />
			</b-modal>
