</template>

<script>
    let api = new URL("http://93.188.162.174:8081");
	api.pathname = "clientes/";
	//api.port = 8000; //Cambien uds los puertos
	//api.port = 8081;

	import VueAwesomplete from "vue-awesomplete";
	import ClientesAlta from "./ClientesAlta.vue";
    import VueExcelXlsx from "vue-excel-xlsx";
    import Vue from "vue";
    Vue.use(VueExcelXlsx);
	import ClientesUpdate from "./ClientesUpdate.vue";
	import ClientesCarnet from "./ClientesCarnet.vue";
	import VueHtml2pdf from "vue-html2pdf";
    const XLSX = require("xlsx");
	import axios from "axios";
	import { APIControler } from "@/store/APIControler";

	export default {
		components: {
			ClientesAlta,
			ClientesUpdate,
			ClientesCarnet,
			VueAwesomplete,
			VueHtml2pdf,
		},
		data() {
			return {
				tabla_clientes: [],
				fields: [
					{ key: "selected", label: "Seleccionar", sortable: true },
					{ key: "numero_cliente", label: "N¬∞ Cliente", sortable: true },
					{ key: "nombre", label: "Nombre/s", sortable: true },
					{ key: "cuitocuil", label: "Cuit o Cuil", sortable: true },
					{ key: "saldopollo", label: "Saldo Pollo", sortable: true },
                    { key: "saldocongelados", label: "Saldo Congelados", sortable: true },
					/*{
						key: "fecha_nacimiento",
						label: "Fecha de Nacimiento",
						sortable: true,
					},*/
					//{key: 'calle' ,label: 'Direccion', sortable: true,},
					//{key:'localidad' ,label: 'Localidad', sortable: true,},
                    { key: "provincia", label: "Provincia", sortable: true },
					//{key:'cod_postal' ,label: 'Codigo Postal', sortable: true,},
					//{key: 'email' ,label: 'Mail', sortable: true,},
					//{key: 'tel_fijo',label: 'Telefono Fijo', sortable: true,},
					//{key:'tel_celular' ,label: 'Telefono Celular', sortable: true,},
					{ key: "reparto", label: "Reparto", sortable: true },
                    { key: "categoria", label: "Categoria", sortable: true },
					{ key: "action", label: "Acciones", variant: "secondary" },
				],
                columns: [
                    { field: "numero_cliente", label: "N¬∞ Cliente"},
                    { field: "nombre", label: "Nombre/s"},
                    { field: "cuitocuil", label: "Cuit o Cuil"},
                    { field: "saldopollo", label: "Saldo Pollo"},
                    { field: "saldocongelados", label: "Saldo Congelados"},
                    { field: "provincia", label: "Provincia"},
                    { field: "reparto", label: "Reparto"},
                    { field: "categoria", label: "Categoria"},
                ],
				filter: null,
				totalRows: 1, //Total de filas
				currentPage: 1, //Pagina actual
				perPage: 10, // Datos en la tabla por pagina
				pageOptions: [10, 20, 40, 100, { value: 10000, text: "Todos" }],
				list_familiares: {},
				datos_familiar: {},
				data: {},

				editar: {},
				buscar: "",
				selected: [],
				infoEliminar: {
					id: "modal_eliminar",
					cliente: -1,
				},
				clienteAldia: [],
				msj_tabla: " Presione 'Mostrar' para ver los regitros ",
				//Botones
				btn_down_pdf: true, //Desabilito los botones, hasta que muestre los datos
				btn_del_full: true,
				btn_mostrar: false,
				btn_editar: false,
				btn_eliminar: false,
				btn_select: false,
				btn_limpiar: true,
                op_categorias: [
                    { value: null, text: "Elija una categoria", disabled: true },
