		mounted: function () {},

		methods: {
			async testFetch() {
                try {
                    const res = await fetch(api);
                    const data = await res.json();

					var lista_clientes = data.results;

                    let mutualesAPI = new APIControler();
                    mutualesAPI.apiUrl.pathname = "repartos/";
					this.listarepa = await mutualesAPI.getData(this.list_repartos);

					for (var i = 0; i < lista_clientes.length; i++)
					{
						lista_clientes[i].reparto = this.getRepartoCliente(lista_clientes[i].numero_cliente);
					}
                    this.tabla_clientes = lista_clientes;
                } catch (error) {
                    console.log(error);
                }
			},
            uploadFile() {
                this.images = this.$refs.archivo.files[0];
                console.log(this.images);
			},
            async importarExcel() {
                const data = await this.images.arrayBuffer();
                let workbook = XLSX.read(data);
                var sheet_name_list = workbook.SheetNames;
                console.log('workbook1');
                console.log(workbook);
                console.log('SheetNames');
                console.log(workbook.SheetNames);
                let ProveedorAPI = new APIControler();
                let datos = XLSX.utils.sheet_to_json(workbook.Sheets[sheet_name_list[0]]);
                console.log("datos");
                console.log(datos);
                ProveedorAPI.apiUrl.pathname = "clientes/";
                datos.forEach(element => ProveedorAPI.postData(element));
                this.testFetch();
            },
			async testFetch2() {
				try {
					this.show = true;

					const res = await fetch(api);
					const data = await res.json();
					var lista_clientes = data.results;

					for (var i = 0; i < lista_clientes.length; i++) {
						var numerillo = await this.getAldia(lista_clientes[i].numero_cliente);
						if (numerillo < 30 && numerillo >= 0) {
							lista_clientes[i].aldia = "‚úî";
						} else {
							lista_clientes[i].aldia = "‚ùå";
						}
					}
					//console.log(lista_clientes[0].aldia);

					this.tabla_clientes = lista_clientes;
					this.btn_down_pdf = false; //Habilito los botones

					if (this.tabla_clientes.length == 0) {
						this.msj_tabla = " No se encuentran regitros en esta tabla ";
