			altaCliente() {},

			async deleteCliente(numero_cliente) {
				axios
                    .delete("http://93.188.162.174:8081/clientes/" + numero_cliente + "/")
					.then((datos) => {
						swal("Operaci√≥n Exitosa", " ", "success");
						console.log(datos);
					})
					.catch((error) => {
						swal("¬°ERROR!", "Se ha detectado un problema ", "error");
						console.log(error);
					})
					.finally(() => this.testFetch());
			},
			getRepartoCliente(numero_cliente)
			{
				let id = 0;
				let retorno = 'Ningun reparto';
				this.listarepa.forEach((element) => {
					element.clientes_reparto.forEach((cliente) => {
						id = parseInt(cliente.split('/')[4]);
						console.log(id);
                        console.log(numero_cliente);
						if (id == numero_cliente) {
                            retorno = element.id_reparto.toString();
                        }
                    });
				});
				return retorno;
			},
			//Funcion para eliminar todos los clientes seleccionados
			async delete_all_Clientes() {
				var cantidad = this.selected.length;

				try {
					for (var i = 0; i < cantidad; i++) {
						axios.delete(
							"http://93.188.162.174:8081/clientes/" +
								this.selected[i].numero_cliente +
								"/"
						);
						if (this.selected.length == 0) {
							console.log("Eliminacion Exitosa");
							break;
						}
					}
					this.hideModal();
					swal("Eliminacion Exitosa", " ", "success");
					this.testFetch();
				} catch (error) {
					this.hideModal();
					swal("¬°ERROR!", "Se ha detectado un problema ", "error");
					console.log(error);
				} finally {
					this.testFetch();
				}
			},
            getCategoria(id) {
                let categoria = this.listacat.find(s => {
                    return s.id_categoria == id
                })
                return categoria;
            },
			//Selecciona una a una
			seleccionar_una(items) {
				this.selected = items;
				if (this.selected.length > 0) {
					this.btn_del_full = false;
					this.btn_limpiar = false;
					if (this.selected.length > 1) {
						this.btn_mostrar = true;
						this.btn_editar = true;
						this.btn_eliminar = true;
					}
					if (this.selected.length == this.rows) {
						this.btn_select = true;
					} else {
						this.btn_select = false;
					}
				} else {
					this.btn_del_full = true;
					this.btn_mostrar = false;
					this.btn_editar = false;
					this.btn_eliminar = false;
					this.btn_limpiar = true;
				}
			},
			//Selecciona todas
			seleccionar_todas() {
				this.$refs.tablaregistros.selectAllRows();
				this.btn_del_full = false;
				this.btn_mostrar = true;
				this.btn_editar = true;
				this.btn_eliminar = true;

				this.btn_select = true;
				this.btn_limpiar = false;
			},
			//Limpia todas las selecciones
			limpiar_seleccion() {
				this.$refs.tablaregistros.clearSelected();
				this.btn_del_full = true;
				this.btn_mostrar = false;
				this.btn_editar = false;
				this.btn_eliminar = false;

				this.btn_select = false;
				this.btn_limpiar = true;
			},

			//Funcion para crear el PDF
			async generarCarnet(item) {
				let data_cliente = {};
				data_cliente.numero_cliente = item.numero_cliente;
				data_cliente.afiliado = item.nombre;
                data_cliente.cuitocuil = item.cuitocuil;
				this.carnetAPDF = data_cliente;
				//this.$refs.html2Pdf.generatePdf();
				this.$refs["modal-carnetCliente"].show();
			},

			onFiltered(filteredItems) {
				// Trigger pagination to update the number of buttons/pages due to filtering
				this.totalRows = filteredItems.length;
				this.currentPage = 1;
			},
			onShown() {
				// Focus the cancel button when the overlay is showing
				this.$refs.cancel.focus();
			},
			onHidden() {
				// Focus the show button when the overlay is removed
				this.$refs.show.focus();
			},


