                clienteAPI.apiUrl.pathname = "clientes/";
				this.respuesta = await clienteAPI.postData(this.cliente);

				//this.resetForm();
				this.cargarFeedback();
				this.updateTable();
			},
            handleInput() {
                this.cliente.provincia = "Tucum√°n";
            },
			cargarFeedback() {
				let valido;
				if (!this.respuesta) this.respuesta = {};
				for (let key in this.validacion) {
					valido = !this.respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					//console.log(key);
					if (!valido) this.validacion[key].mensaje = this.respuesta[key][0];
				}
			},
		
			async getRepartos() {
				let mutualesAPI = new APIControler();
				mutualesAPI.apiUrl.pathname = "repartos/";
				this.data = await mutualesAPI.getData(this.list_repartos);
				this.data.forEach((element) => {
					let option = {};
					option.value =
						"http://93.188.162.174:8081/repartos/" + element.id_reparto + "/";
					option.text =
						element.id_reparto +
						"-- " +
						element.nombre
					console.log(option);
					this.op_repartos.push(option);
				});
			},
            async getCategorias() {
                let mutualesAPI = new APIControler();
                mutualesAPI.apiUrl.pathname = "categorias/";
                this.data = await mutualesAPI.getData();
                this.data.forEach((element) => {
                    let option = {};
                    option.value =
                        "http://93.188.162.174:8081/categorias/" + element.id_categoria + "/";
                    option.text =
                        element.letra +
                        "-- " +
                        element.limite
                    console.log(option);
                    this.op_categorias.push(option);
                });
            },
			async resetForm() {
				this.cliente.numero_cliente = null;
				this.cliente.nombre = "";
				this.cliente.cuitocuil = null;
				this.cliente.calle = "";
				this.cliente.localidad = "";
				this.cliente.departamento = "";
				this.cliente.cod_postal = null;
				this.cliente.tel_fijo = null;
				this.cliente.tel_celular = null;
				this.cliente.saldopollo = null;
                this.cliente.saldocongelados = null;
			},
			async resetFormAdh() {
				/*
        this.familiar.apellido = "";
        this.familiar.nombre = "";
        this.familiar.dni_familiar = null;
        this.familiar.fecha_nacimiento=null;
        this.familiar.carencia= null;
        */
			},
		},
		beforeMount() {
			this.getRepartos();
            this.getCategorias();
			this.handleInput();
		},
	};
</script>

